body {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), no-repeat 50% 50% url(../img/img/fon-image.png);
  background-size: cover;
}

.page-header {
  padding-top: 43px;
  margin-bottom: 25px;
  padding-bottom: 35px;
  padding-left: 20px;
  border-bottom: 1px solid $border-color;
  
  // стилі для десктоп

  @media (max-width: $tablet-width) {
    padding-top: 43px;
    padding-bottom: 35px;
    padding-left: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid $border-color;
  }
}

.page-header__logo {
  padding: 0 96px;

  @media (max-width: $tablet-width) {
    padding: 0;
  }
}